<template>
   <div class="ContContent__BxRight--BxHeader">
      <div class="BxBrand">
        JomBosa
      </div>
      <div class="BxHeaderItems">
         <div class="BxSearch">
            <div id="SeachIn" title="Search">
	            <input type="search" @keyup.enter="BtnSearch"  v-model="TextSearch" >
            </div>
         </div>

         <div class="ArNotif">
            <div class="BxNotifIcon">
               <v-badge  
               color="transparent"
               overlap >
                  <template v-slot:badge>0</template>
                  <v-icon @click="NotifBox = !NotifBox" title="Notification">fa fa-cogs</v-icon>
               </v-badge>
            </div>

            <transition name="fade">
               <v-app class="BxNotif" v-if="NotifBox">
                  <v-card
                     class="mx-auto BxNotifCard"
                     shaped
                     outlined
                  >
                     <v-list-item >
                        <v-list-item-content>
                           <div class="overline mb-4">Genre Movie</div>
                           
                           <div class="BxGenre">
                              <span>Action</span>
                              <span>Adventure</span>
                              <span>Animation</span>
                              <span>Biography</span>
                              <span>Comedy</span>
                              <span>Crime</span>
                              <span>Documentary</span>
                              <span>Drama</span>
                              <span>Family</span>
                              <span>Fantasy</span>
                              <span>Film-Noir</span>
                              <span>History</span>
                              <span>Horror</span>
                              <span>Music</span>
                              <span>Musical</span>
                              <span>Mystery</span>
                              <span>Reality-TV</span>
                              <span>Romance</span>
                              <span>Sci-Fi</span>
                              <span>Science Fiction</span>
                              <span>Short</span>
                              <span>Sport</span>
                              <span>Thriller</span>
                              <span>TV Movie</span>
                              <span>War</span>
                              <span>Western</span>
                           </div>
                        </v-list-item-content>

                     </v-list-item>

                  </v-card>
               </v-app>
            </transition>

         </div>


         
      </div>
   </div>
  
  
</template>

<script>
   export default {

      data: () => ({ 
         NotifBox:false,
         TextSearch:null
      }),
   
      methods: {
         BtnSearch(){
            const varPage = 1
            const NmMovie = this.TextSearch
            this.$store.dispatch('GET_SEARCH_MOVIE', NmMovie, varPage);
            this.$store.commit('MutValSearch', NmMovie);
            this.$store.commit('MutLoaderDt', true);
         }
      },
   }
</script>

<style lang="scss">
	@import './Header.scss';
</style>


